.page-container{margin:90px auto;max-width:720px;min-width:360px;padding:0 30px;position:relative}.posts-pagination{display:flex}.posts-pagination,.posts-pagination-btn{align-items:center;justify-content:center}.posts-pagination-btn{border:3px solid #bdbdbd;border-radius:3px;color:#bfbfbf;cursor:pointer;display:inline-flex;padding:10px 18px;transition:all .2s ease}.posts-pagination-btn:hover{border:3px solid #616161;color:#616161}.post-is-original{border-radius:11px;display:inline-block;font-size:13px;line-height:22px;margin-left:-33px;margin-right:10px;padding:0 6px}@media only screen and (max-width:768px){.post-is-original{display:none}}